<template>
	<div id="estore-order-search-result">
		<public-header title="搜索结果">
			<div slot="right" class="search-btn" @click="util.jsUrl('/plugin/pages/paotui/store/order/search')">
				<span class="icon icon-search font-20"></span>
			</div>
		</public-header>
		<public-footer :menufooter="menufooter" :showPreLoading="showPreLoading"></public-footer>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
				>
					<template v-for="(item, index) in records.data">
						<order-item
							:order="item"
							:order_index='index'
							@onChangeStatus='onChangeStatus'
						></order-item>
					</template>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>暂无符合条件订单!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex';
	import publicHeader from "@/components/header";
	import publicFooter from "@/components/footer";
	import orderItem from "../components/orderItem";
	import {getOrderList, changeOrderStatus} from "../controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			orderItem
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				records: {
					page: 1,
					psize: 15,
					finished: false,
					loading: false,
					data: []
				},
				menufooter: {}
			}
		},
		methods: {
			...mapMutations([]),
			onLoad(force) {
				let that = this;
				getOrderList({
					vue: that,
					force: force,
					data: {
						filter: that.filter.items,
						menufooter: 1
					}
				});
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
			onChangeStatus(options) {
				options.vue = this;
				options.from = 'search';
				changeOrderStatus(options);
			},
		},
		computed: {
			...mapState([
				'erranderStoreOrderSearch',
			]),
			filter() {
				var filter = {
					items: {}
				}
				filter = this.util.extend(filter, this.erranderStoreOrderSearch);
				return filter;
			}
		},
		mounted() {
			this.onLoad(false);
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.status = this.query.status;
			}
		}
	}
</script>

<style>

</style>


